import * as React from 'react';
import { hotkeys, Controller } from '@ali/4ever-cangjie';

export default function onKeyDown(event: React.KeyboardEvent, controller: Controller, next: Function) {
  if (hotkeys.isUndo(event)) {
    event.preventDefault();
    return controller.dispatch('undo');
  }

  if (hotkeys.isRedo(event)) {
    event.preventDefault();
    return controller.dispatch('redo');
  }

  return next();
}
